<template>
  <div v-if="state" class="comp guide_mask"></div>
</template>

<script>
import { mapState } from 'vuex'
import { jump } from '@/utils/linkMap.js'

// '13001' => '个人中心-充值订单历史记录',
// '13002' => '个人中心-提现订单历史记录',
// '13003' => '个人中心-密码修改',
// '13004' => '个人中心-打码量进度查看',
// '13005' => '游戏大厅-余额',
// '13006' => '游戏大厅-下载',
// '13007' => '语言切换按钮',
// '13008' => '活动中心',
// '13009' => '分享界面-Bonus界面',
// '13010' => '分享界面-宝箱',
// '13011' => '代理客服联系界面',
// '13012' => '下级邀请赠送活动详情页面',
// 需要引导的内链
const insideCode = [
  {
    code: 13001, // 对应的code
    appId: 'depositRecordId', // 元素的id - app
    pcId: 'depositRecordId', // 元素的id - pc
    x: 'center', // 元素滚动位置 x 轴
    y: 'center' // 元素滚动位置 y 轴
  },
  {
    code: 13002,
    appId: 'withdrawRecordId',
    pcId: 'withdrawRecordId',
    x: 'center',
    y: 'center'
  },
  {
    code: 13003,
    appId: 'accountPwdId',
    pcId: 'accountPwdId',
    x: 'center',
    y: 'center'
  },
  {
    code: 13004,
    appId: 'washScheduleId',
    pcId: 'washScheduleId',
    x: 'center',
    y: 'center'
  },
  {
    code: 13005,
    appId: 'bankAccountId',
    pcId: 'bankAccountId',
    x: 'center',
    y: 'start'
  },
  {
    code: 13006,
    appId: 'fabDownloadAppId',
    pcId: 'fabDownloadPcId',
    x: 'end',
    y: 'end'
  },
  {
    code: 13007,
    appId: 'languageSelectId',
    pcId: 'languageSelectId',
    x: 'center',
    y: 'center'
  },
  {
    code: 13008,
    appId: '',
    pcId: '',
    x: 'center',
    y: 'center'
  },
  {
    code: 13009,
    appId: '',
    pcId: '',
    x: 'center',
    y: 'center'
  },
  {
    code: 13010,
    appId: 'chestCardId',
    pcId: 'chestCardId',
    x: 'center',
    y: 'center'
  },
  {
    code: 13011,
    appId: '',
    pcId: '',
    x: 'center',
    y: 'center'
  },
  {
    code: 13012,
    appId: 'moneySignCardId',
    pcId: 'moneySignCardId',
    x: 'center',
    y: 'center'
  }
]

export default {
  props: {
    // 内外链
    item: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  data() {
    return {
      state: false
    }
  },
  computed: {
    ...mapState('base', ['isApp'])
  },
  methods: {
    // 打开
    show() {
      this.state = true
      // 关闭充值提现弹窗
      this.$store.dispatch('modal/setDepositWithdrawShow', { show: false })

      jump(this.item, this)

      this.$nextTick(() => {
        setTimeout(() => {
          const { inside_data = {} } = this.item
          const { id = '' } = inside_data
          const insideItem = insideCode.find(f => f.code == id)
          if (!insideItem) return

          const { appId, pcId, x, y } = insideItem
          const dom = document.getElementById(this.isApp ? appId : pcId)

          // 滚动条滚动到相对区域
          if (dom) dom.scrollIntoView({ block: x || 'center', inline: y || 'center' })
        }, 200)
      })

      this.hide()

      /**
       * 高亮 --> 待完善，做了一半，产品说不要了
       */
      // const { inside } = this.item;
      // const insideItem = insideCode.find(f => f.code == inside);
      // if (!insideItem) return;

      // const { appId, pcId } = insideItem;
      // this.guideHighlight(this.isApp ? appId : pcId);
    },
    // 关闭
    hide() {
      this.state = false
      if (this.remove) this.remove()
    },
    // 引导高亮
    guideHighlight(domId = '') {
      if (!domId) return

      this.$nextTick(() => {
        const dom = document.getElementById(domId)
        if (!dom) return

        // 滚动条滚动到相对区域
        setTimeout(() => {
          dom.scrollIntoView({
            behavior: 'smooth',
            block: 'center',
            inline: 'center'
          })
        }, 200)

        // 添加高亮 class
        dom.classList.add('guide_highlight')
        // 监听点击事件
        this.domId = domId
        const appBox = document.getElementById('app_box')
        appBox.addEventListener('click', this.guideFun)
      })
    },
    // 监听引导高亮函数
    guideFun() {
      // 关闭
      this.hide()
      // 卸载监听点击事件
      const appBox = document.getElementById('app_box')
      appBox.removeEventListener('click', this.guideFun)

      /**
       * 去除引导高亮
       */
      const dom = document.getElementById(this.domId)
      if (!dom) return
      // 删除高亮 class
      dom.classList.remove('guide_highlight')
    }
  }
}
</script>

<style lang="scss" scoped></style>
